/* Hero Block */

.block--hero {
    display: none;
    position: relative;
    overflow: hidden;
    border: 0;
    padding: 0;
    z-index: 0;
    .parallax-container {
        position: relative;
        overflow: hidden;
        height: 14rem;
        background-color: rgba(0,0,0,.7);
        /*padding-bottom: $offset-height;*/
        .parallax {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            div,
            article {
                height: 100%;
            }
            img {
                left: 50%;
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
                bottom: 0;
                min-height: 100%;
                min-width: 100%;
                max-width: 150%;
                position: absolute;
                @include media-breakpoint-up(md) {
                    max-width: 125%;
                }
                @include media-breakpoint-up(lg) {
                    max-width: 100%;
                }
                &.resizing {
                    top: 50%;
                    -webkit-transform: translate(-50%, -50%) !important;
                    transform: translate(-50%, -50%) !important;
                }
            }
        }
        .row {
            max-width: 900px;
            margin: 0 auto;
            @include media-breakpoint-up(sm) {
                margin: 0 1rem;
            }
            @include media-breakpoint-up(md) {
                margin: 0 2rem;
            }
            @include media-breakpoint-up(lg) {
                margin: 0 3rem;
            }
            @include media-breakpoint-up(xl) {
                margin: 0 auto;
            }
            .block--hero__content__main__content {
                margin: 0 auto;
            }
        }
        .block--hero__content {
            &__breadcrumbs {
                .col-xl-12 {
                    padding: .5rem 3rem;
                    a, i {
                        color: $color-white;
                    }
                    a {
                        border-bottom: 0.0625rem solid $color-white;
                    }
                }
            }
        }
    }
}