.image--parallax {
    position: relative;
    overflow: hidden;
    height: auto;

    .parallax {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        
        img {
            opacity: 0;
            position: absolute;
            left: 50%;
            bottom: 0;
            min-width: 100%;
            min-height: 100%;
            max-width: 100%;
            object-fit: cover;
            transform: translate3d(0,0,0);
            transform: translateX(-50%);
        }
    }
}