.region--top-two-col {
    &__left-column {
        @include media-breakpoint-up('md') {
            padding-left: 0;
        }
    }

    &__right-column {

        .paragraph__right-img {
            padding-left: 0 !important;
            max-width: none !important;
            float: right !important;
        }

        @include media-breakpoint-up('md') {
            padding-right: 0;

            .paragraph__right-img {
                padding-left: .5rem !important;
            }
        }
    }
}