// Variables

// COLORS

// Global
$color-white: #fff;
$color-black: #000;
$color-niaid-blue: #20558a;
$color-success: #078A00;

// Layout
$page-background: #FDFDFD;
$page-background-alternate: #F5F6FA;

// Type
$type-heading-color: #2F2F2F;
$type-body-color: #404B56;
$type-max-width: 88ex;
$type-link-color: #246CD3;
$type-link-underline-color: #777777;
$link-visited: #6F57B5;

// Input
$input-checkmark-border: #5A5A5A;
$input-border-color: #888888;
$input-placeholder-color: #9AA6B5;
$input-error-color: #C9191C;

// Border Radius
$border-radius: .3125rem;

// Font Weight
$fw-thin: 100;
$fw-light: 300;
$fw-regular: 400;
$fw-medium: 500;
$fw-bold: 700;
$fw-black: 900;

// Breakpoints & Sizing
$mobile-menu-breakpoint: 950px;
$body-max-width: 1200px;

