.layouts--tabs {
    #layouts--tabs__style-tile {
        display: none;
    }
    &__tab {
        border-radius: .5rem .5rem 0 0;
        border: 0.0625rem solid $color-white;
        background: transparent;
        color: $color-white;
        font-size: 1.25rem;
        padding: .75rem 3rem;
        margin-bottom: -0.0625rem;
        z-index: 1;
        position: relative;
        cursor: pointer;
        &.active-tab {
            background: $color-white !important;
            color: $type-body-color !important;
            font-weight: $fw-bold;
            cursor: initial;
        }
        &:hover,
        &:focus {
            color: $color-white;
            background: $color-theme-1-1-1;
        }
    }
    &__window {
        >.layouts--body-card {
            @include md-16dp;
            z-index: 0;
            position: relative;
        }
    }
}