.navigation--mobile-rail {
    &__button {
        @extend .button-pl;
        @extend .button--secondary;
        width: 100%;
        text-align: center;
        background: transparent;
        border-radius: 0 !important;
        cursor: pointer;
        @include media-breakpoint-up(lg) {
            display: none !important;
        }
    }
    &__content {
        @include media-breakpoint-up(lg) {
            display: block !important;
        }
    }
}