.social-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 1.8em;
    height: 1.8em;
    margin-top: .8rem;
    margin-right: .8rem;
    border-radius: 100%;
    background: #ffffff;
    text-decoration: none;
    @include md-2dp;
    @extend .hoverable;

    &:hover {
        text-decoration: none;
    }

    i {
        color: #ffffff;
    }

    &--facebook-f {
        background: #3B5998;
    }

    &--instagram {
        background-image: url('../images/instagramBG.png');
        background-position: center;
    }

    &--twitter {
        background: #00ACED;
    }

    &--linkedin-in {
        background: #0077B5;
    }

    &--youtube {
        background: #FF0000;
    }

    &--pinterest-p {
        background: #C92228;
    }

    &--flickr {
        background: #FF0084;
    }

    &--google-plus-g {
        background: #D34836;
    }
}