a {
    color: $type-link-color;
    border-bottom: 1px solid $type-link-underline-color;
    &:hover,
    &:focus {
        color: $type-link-color;
        text-decoration: none;
        border-bottom: 1px solid transparent;
    }
    /*&:visited {
        color: $link-visited;
        &:hover,
        &:focus {
            color: $link-visited;
        }
    }*/
}

p,
ul,
ol {
    margin-top: 1rem;
    margin-bottom: 1rem;
    max-width: $type-max-width;
}

ul {
    list-style-type: circle;
    li {
        margin-bottom: .25em;
        >ul {
            margin-top: 0;
        }
    }
}