.global--header {
    height: 6.25rem;
    position: relative;
    z-index: 100;

    &__overlay {
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+1,ffffff+100&1+10,0.9+100 */
        /* IE9 SVG, needs conditional override of 'filter' to 'none' */
        background: url(data:image/svg+xml;base64,);
        background: -moz-linear-gradient(top,  rgba(255,255,255,1) 1%, rgba(255,255,255,1) 10%, rgba(255,255,255,0.9) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 1%,rgba(255,255,255,1) 10%,rgba(255,255,255,0.9) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  rgba(255,255,255,1) 1%,rgba(255,255,255,1) 10%,rgba(255,255,255,0.9) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#e6ffffff',GradientType=0 ); /* IE6-8 */
        height: 100%;

        &__row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 100%;
            padding: 0 1rem;

            @include media-breakpoint-up(sm) {
                padding: 0 1.5rem;
            }

            @include media-breakpoint-up(lg) {
                padding-left: calc(25px + 2rem);
                padding-right: calc(25px + 2rem);
            }
    
            .image--logo {
                &__image {
                    height: 2.5rem;
                    width: auto;

                    @include media-breakpoint-up(lg) {
                        height: 3.125rem;
                    }
                }
            }
        }
    }
}