.block--alert {
    @include md-0dp;
    background: #be2c2c !important;
    color: $white;
    border-radius: 0 !important;
    border: 0 !important;
    margin: 0;
    padding: 1rem;
    * {
        color: $white !important;
    }
    h1 {
        font-size: 1.75rem;
        margin-bottom: .75rem;
        margin-top: 0;
    }
    a {
        border-bottom: 0.0625rem solid $white;
        &:hover,
        &:focus {
            border-bottom: 0.0625rem solid transparent;
        }
        &:visited {
            color: $white !important;
        }
    }
    hr {
        background: $white;
        margin: .75rem 0;
    }
    p {
        margin: .35rem 0;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &.severity--2, &.severity--3 {
        * {
            color: $black !important;
        }
        hr {
            background: $black;
        }
        a {
            border-bottom: 0.0625rem solid $black;
            &:visited {
                color: $black !important;
            }
        }
    }
    &.severity--2 {
        background: #ff8c00 !important;
    }
    &.severity--3 {
        background: #ffff00 !important;
    }
    &.severity--4 {
        background: #008000 !important;
    }
}