.button-pl,
button {
    cursor: pointer;
    font-weight: $fw-regular;
    text-decoration: none;
    border: 0;
    padding: 1rem 2rem;
    line-height: initial;
    border-radius: 0;
}

button {
    @extend .button--primary;
}

.disabled,
button:disabled,
button[disabled] {
    @extend .btn;
    border-color: transparent !important;
    cursor: not-allowed;
}