.link--icon {
    text-decoration: none;
    border-bottom: 0;

    &--left, &--right {
        display: inline;
    }

    &__text {
        &:hover, &:focus {
            border-bottom: 0.0625rem solid $type-link-underline-color;
        }
    }

    &--left {
        @extend .mr-2;
    }

    &--right {
        @extend .ml-2;
    }
}

.icon--social {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 1.8rem;
    height: 1.8rem;
    margin-bottom: .8rem; 
    border-radius: 100%;
    background: #ffffff;
    text-decoration: none;
    border-bottom: 0 !important;
    &:hover,
    &:focus {
        text-decoration: none;
        border-bottom: 0 !important;
    }
    i {
        color: #ffffff;
    }
    &--facebook-f {
        background: #3B5998;
        .icon--default {
            &:before {
                content: "\f39e" !important;
            }
        }
    }
    &--instagram {
        background-image: url('../images/instagramBG.png');
        background-position: center;
    }
    &--twitter {
        background: #00ACED;
    }
    &--linkedin-in {
        background: #0077B5;
    }
    &--youtube {
        background: #FF0000;
    }
    &--pinterest-p {
        background: #C92228;
    }
    &--flickr {
        background: #FF0084;
    }
    &--google-plus-g {
        background: #D34836;
    }
    &--comment,
    &--envelope,
    &--copyright {
        i {
            color: $blue;
        }
    }
}