.input-pl {
    &::-webkit-input-placeholder {
        /* Chrome/Opera/Safari */
        font-style: italic;
        color: $input-placeholder-color;
    }
    &::-moz-placeholder {
        /* Firefox 19+ */
        font-style: italic;
        color: $input-placeholder-color;
    }
    &:-ms-input-placeholder {
        /* IE 10+ */
        font-style: italic;
        color: $input-placeholder-color;
    }
    &:-moz-placeholder {
        /* Firefox 18- */
        font-style: italic;
        color: $input-placeholder-color;
    }
}