.block--full-palette {
    padding-top: 2rem;
    margin: 0;
    border: 0;
    &__set {
        display: inline-block;
        margin: 0 1rem;
        &:first-child {
            margin-left: 0;
        }
        &__block {
            height: 2.5rem;
            width: 4rem;
            background: transparent;
            &__label {
                position: relative;
                color: $color-white;
                font-size: .8rem;
                top: 50%;
                transform: translateY(-50%);
                text-align: center;
            }
        }
    }
}