/* Headings */

h1,
h2,
h3,
h4,
h4,
h5,
h6 {
    font-weight: $fw-regular;
    @include antialias;
    line-height: 1.3;
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: $type-heading-color;
    a {
        border-bottom: 0;
        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }
}

h1 {
    margin: 0 0 2rem 0;
    font-size: 4rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 1.65rem;
}

h5 {
    font-weight: $fw-bold;
}

h6 {
    font-size: 1.25rem;
}